.container {
  max-width: 1400px;
  display: flex;
  margin: 60px auto;
  justify-content: space-between;
}
.socialWrap{
    width: 100%;
}
.social {
  display: flex;
  flex-direction: column;
}
.email {
  display: flex;
  flex-direction: column;
}
.blockRed {
  max-width: 680px;
  height: 180px;
  background: #f01b0d;
  border-radius: 2px;
  display: flex;
  justify-content: center;
}
.blockBlack {
    position: relative;
  max-width: 680px;
  height: 180px;
  background: #000000;
  border-radius: 2px;
  /* display: flex;
  justify-content: center; */

}
.headSocial {
  padding-bottom: 24px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #000000;
}
.wrapIcons {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 50px;
  max-width: 460px;
}
.formInput{
    width: 50%;
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: white;
    outline: none;
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.btn{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px;
    gap: 10px;
    width: 25%;
    height: 40px;
    background: #F01B0D;
    box-shadow: 0px 1px 8px rgba(3, 0, 172, 0.1);
    border-radius: 100px;

}
.wrapInpBtn{
    display: flex;
    align-items: flex-end;
    justify-content: space-evenly;
    position: absolute;
    top: 40%;
    width: 100%;
    
}
.textBtn{
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 14px;
letter-spacing: 0.05em;
text-transform: uppercase;
color: #FFFFFF;
}