._container {
  max-width: 1400px;
  padding: 0px 15px;
  margin: 0px auto;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.header {
  max-width: 112px;
  height: 21px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #000000;
  margin: 40px 0px 24px 0px;
}
.firstArticle {
}
.dateContent {
  margin-top: 20px;
}
.date {
  max-width: 55px;
  height: 14px;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  color: #828282;
  flex: none;
  order: 0;
  flex-grow: 0;
}
.dateFill {
  max-width: 101px;
  height: 13px;
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #000000;
  flex: none;
  order: 1;
  flex-grow: 0;
}
.titleContent {
  max-width: 640px;
  /* height: 64px; */
  margin: 10px 0px;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 123%;
  letter-spacing: -0.01em;
  color: #1f1f1f;
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
}
.fillContent {
  max-width: 640px;
  height: 75px;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 125%;
  color: #828282;
  flex: none;
  order: 2;
  align-self: stretch;
  flex-grow: 0;
}
.contentFotoBalck {
  position: relative;
  z-index: -1;
  top: -22px;
  left: -16px;
}

.articleFoto {
}
.blackBack {
  position: absolute;
  bottom: 75px;
  padding: 0px 20px 0px 20px;
}
